.body{
    padding:10px 15px;
}
div.body-grid{
    display: grid;
    grid-template-columns: 1fr 3fr 5fr 3fr;

}
div.thumbnail img{
    height: 78px;
    width:52px;
    margin-bottom: 5px;
    border: 1px solid rgb(221, 221, 221);
    border-radius: 2px;
}
div.thumbnail img:hover{
    box-shadow: 2px 2px 4px #e77600;
    border: 1px solid #e77600;
    border-radius: 2px;
}
div.thumbnail{
    display: flex;
    flex-direction: column;
}
div.descrip{
    padding-left:10px;
}
div.descrip h1{
    padding:0px;
}
ul.skill li{
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 20px;
}
div.buy {
    display: flex;
    justify-content: center;
    margin-left: 15px;
}
div.buy button{
    border-radius: 20px;
}